const urlParams = new URLSearchParams(window.location.search)
const videoUrl = urlParams.get('url') ? atob(urlParams.get('url')) : 'demo.mp4'

// DOM元素
const sourceVideo = document.getElementById('sourceVideo')
const previewVideo = document.getElementById('previewVideo')
const cutBtn = document.getElementById('cutBtn')
const exportBtn = document.getElementById('exportBtn')
const timelineMarkers = document.getElementById('timelineMarkers')

let cliper = null
let trimStart = 0
let trimEnd = 0

// 初始化加载视频
sourceVideo.src = videoUrl
sourceVideo.onloadeddata = async () => {
  cliper = new Clip(sourceVideo) // <sup data-citation='{&quot;id&quot;:1,&quot;url&quot;:&quot;&lt;!DOCTYPE html&gt; &lt;html da...lass=videoAiLogo&gt;&lt;/div&gt;&quot;,&quot;title&quot;:&quot;&lt;!DOCTYPE html&gt; &lt;html da...lass=videoAiLogo&gt;&lt;/div&gt;&quot;,&quot;content&quot;:&quot;K EN OF CONTENTS 安装依赖 项目目录 更进一步 开始介绍 v1 版本 install webavav-cliper npm install webavav-canvas 如果你先项目依赖多个 webav 下的包，务必让它们的版本号保持一致。 项目目录 - doc-site 文档站点 - docs md - packages pnpm 管理的 monorepo，阅读模块介绍章节了解更&quot;}'>1</sup><sup data-citation='{&quot;id&quot;:4,&quot;url&quot;:&quot;&lt;!DOCTYPE html&gt; &lt;html da...lass=videoAiLogo&gt;&lt;/div&gt;&quot;,&quot;title&quot;:&quot;&lt;!DOCTYPE html&gt; &lt;html da...lass=videoAiLogo&gt;&lt;/div&gt;&quot;,&quot;content&quot;:&quot;aWxsPSIjMmMzZTUwIi8+CiAgICAgICAgICAgIDwvZz4KICAgICAgICAgIDwvZz4KICAgICAgICA8L2c+CiAgICAgIDwvZz4KICAgICAgPGcgaWQ9Ikdyb3VwXzE2MCI+CiAgICAgICAgPGcgaWQ9IkdyYXBoaWNfMTYzIj4KICAgICAgICAgIDxwYXRoIGQ9Ik0gODA1&quot;}'>4</sup>
  
  // 创建时间轴标记
  createTimeline(sourceVideo.duration)
}

// 创建时间轴标记
function createTimeline(duration) {
  timelineMarkers.innerHTML = ''
  for (let i = 0; i < duration; i += 3) {
    const marker = document.createElement('div')
    marker.className = 'timeline-marker'
    marker.textContent = `${i}s`
    marker.style.left = `${(i/duration)*100}%`
    timeline.addEventListener('click', () => handleMarkerClick(i))
    timelineMarkers.appendChild(marker)
  }
}

// 剪切功能实现
cutBtn.addEventListener('click', async () => {
  if (!cliper) return
  
  // 设置剪切范围（根据实际UI获取时间点）
  await cliper.setRange(trimStart, trimEnd)
  
  // 预览效果
  const resultStream = await cliper.cutToMediaStream()
  previewVideo.srcObject = resultStream
})

// 导出功能实现
exportBtn.addEventListener('click', async () => {
  if (!cliper) return
  
  // 导出视频文件
  const output = await cliper.exportToFile('edited.mp4')
  
  // 创建下载链接
  const downloadLink = document.createElement('a')
  downloadLink.href = URL.createObjectURL(output)
  downloadLink.download = '剪辑后的视频.mp4'
  downloadLink.click()
})
